.projets ul {
    display: flex; 
    list-style-type: none;
    justify-content: center; 
    align-items: center;
    flex-wrap: wrap;
}

.projets ul li {
    flex: 0 1 2rem;
    padding: 0; 
    margin: 0.5rem; 
}

.projets ul li img {
    max-width: 14rem;
    max-height: 20rem; 
}