*{
    margin: 0;
    padding: 0;
    box-sizing: border-box; 
   
}

body {
    margin: 0;
    overflow-x: hidden;
    font-family: 'Century Gothic', sans-serif;
}

        /* NAVBAR */ 

nav {
    position: fixed;
    display: flex;
    align-items: center;
    min-height: 6vh; 
    background: #fff;
    border-bottom: 1px solid #bdc3c7; 
    width: 100%;  
    position: absolute;
    top: 0;
    z-index: 15;
} 

i {
    font-size: 18px;
}

.logo {
    color: #000; 
    text-transform: uppercase; 
    letter-spacing: 5px;
    font-size: 20px; 
    padding: 20px;
} 

.logo h4 a {

    text-decoration: none;
    color: #000; 
} 

.logo h4 a:hover {
    text-decoration: none;
    color: #000;
}

ul li {
    list-style: none;
    display: inline-block;
    padding: 10px;
   
}

ul li a {
    color: #000;
    text-decoration: none; 
    font-size: 15px; 
    transition: 0.1s;
    padding-bottom: 25px;
    padding-top: 10px;
}

ul li a:hover {
    color: #19b4fa;
    padding-bottom: 32px;
    text-decoration: none;
}

.ul1{
    float: left;
}

.ul2 {
    position: absolute;
    right: 0px;
}

.connecter-icone{
    display: none; 
}

.connecter, .connecter-icone {
    border: 1px solid #19b4fa; 
    padding: 15px;
    color: #fff; 
    background: #19b4fa;  
    transition: 0.2s; 
} 

.connecter-icone {
    margin-top: 10px;
    padding: 10px 15px 10px 15px;
}

.connecter:hover, .connecter-icone:hover{
    border: 1px solid #1391cc; 
    background: #1391cc; 
    color: #fff; 
    padding-bottom: 16px;
} 

.connecter-icone:hover {
    margin-top: 10px;
    padding: 10px 15px 10px 15px;
} 

.burger {
    margin: 10px;
    border: 1px solid #19b4fa;
    background: #19b4fa;
    padding: 5px; 
    display: none; 
}

.burger div {
    width: 22px;
    height: 3px; 
    background: #fff;
    margin: 5px; 
}

.burger:hover {
    border: 1px solid #1391cc; 
    background: #1391cc;
}

.burger-menu {
    display: none; 
    flex-direction: column; 
    align-items: center;
    position: fixed;
    margin-top: auto;
    top: 8.5vh;
    left: 0;
    background: #fff; 
    width: 50%; 
    height: auto;
   /* transform: translateX(-100%); */
    opacity: 0; 
    transition: opacity 0.1s ease-in;
    z-index: 20;
}

.menu-active {
    /*  transform: translateX(0); */
    display: flex;
    opacity: 1; 
}

.burger-menu li 
{
    padding: 20px 5px 20px 5px;
    border-bottom: 1px solid #bdc3c7;
    width:100%; 
} 

.burger-menu li a {
    margin-left: 15%;
    font-size: 14px;
}

.button {
    padding: 15px 5% 15px 5%;
    border: 1px solid #fff; 
    transition: .3s;
}

.primary {
    border: 1px solid #19b4fa; 
    background: #19b4fa; 
    color: #fff; 
}

.primary:hover{
    border: 1px solid #1391cc; 
    background: #1391cc;
}

        /* LANDING */ 

.header{
    background-image: url('./img/land.jpg'); 
    background-size: cover; 
    height: 90vh 
}
.header .landing-button  {
    position: relative;
    color: #fff;
    text-align: center; 
    vertical-align: middle;
}

.header .landing-button {
    justify-content: center;
    align-items: center;
    top: 40vh; 
}


@media screen and (max-width: 1140px) {
    .connecter{
        display: none; 
    }

    .connecter-icone {
        display: block; 
    }

    .ul2{
        right: 2px;
    }
}

@media screen and (max-width: 800px) {
    .ul1 {
        display: none; 
    }
    .burger{
        display: block; 
    }
}

@media screen and (max-width: 600px) {
    span {
        display: none;
    }
}

        /* SECTION 1 */
.section1 {
    display: flex;
    margin: 20px;
}  

.laune {
    flex: 50%; 
    height: 40vh; 
}

.selection1 {
    flex: 40%;
    margin-left: 20px; 
} 


    /* Style for card */ 

.card .card-img {
    border: 1px solid transparent; 
    overflow: hidden;
}

.card .card-img img   {
    transform: scale(1); 
    transition: .4s; 
} 

.card .card-img img:hover   {
    transform: scale(1.08); 
} 

.selection1 .card .card-img img {
    width: 15em; 
    margin-right: 20px;
}

@media screen and (max-width: 1024px) {
    .section1 {
        flex-direction: column;
    }
    .laune {
        margin: 20px;
    }
    
    .laune img {
        width: 100%;
    } 

    .selection1 .card {
        display: flex; 
        flex-direction: row;
        margin-bottom: 5vh; 
    } 

    .selection1 .card .card-img {
        flex: 15em; 
    }

    .selection1 .card .card-descrip{
        flex: 30%; 
        margin-left: 20px; 
    }

} 

.section2 h2{
    font-size: 30px;
    text-align: center;
    margin-bottom: -10vh; 
} 

  /* Section3 */ 


.section3 {
    display: flex; 
} 

.section3 .img-descrip-barkelou {
    flex: 40%; 
    margin-left: 20px;
}

.section3 .img-descrip-barkelou img{
    width: 90%; 
    height: 90vh;
}

.section3 .descrip-barkelou {
    flex: 40%; 
}

.section3 .descrip-barkelou h2{
    font-size: 40px;
 } 

 .section3 .descrip-barkelou ul {
     margin-top: 10px; 
 } 

.section3 .descrip-barkelou ul li {
     margin-bottom: 20px; 
 } 
 
 
@media screen and (max-width: 1024px) {
    .section3 {
        flex-direction: column;
    } 

    .section3 .img-descrip-barkelou {
        margin-right: 20px; 
    }

    .section3 .img-descrip-barkelou img{
        width: 100%; 
        margin-right: 20px; 
    }

    .section3 .descrip-barkelou {
        margin-top: 20px;
    }

    .section3 .descrip-barkelou h2 {
        text-align: center; 
    }

    .section3 .descrip-barkelou ul {
        justify-content: center; 
    }
}

    /* Bas de page */ 

footer {
    background: rgb(27, 26, 26); 
    height: 40vh;
    width: 100%;
} 

footer h2 {
    color:  #fff; 
    padding: 30px; 
}

.tab {
    background: #ecf0f1;
    padding: 20px;
}


    /* PAGE DECOUVERTE */


h2 .carousel-img {
  overflow: hidden;
}

h2 .carousel-img img  {
   
    border-radius: 5px; 
    position: relative; 
    top: 20vh;
    height: 10em;
    width: 80%;
 
    transform: scale(1); 
    transition: 0.4s; 
}  

 h2 .carousel-img img:hover {
    transform: scale(1.04); 
}  

.slider {

    justify-content: space-between;
    overflow: hidden;
    margin-left: 6%;
    margin-right: 2%;
    text-align: center;
}


.prev, .next{
    outline: none; 
    border: 1px solid #000; 
    height: 40px;
    width: 40px;
    border-radius: 50%; 
    margin: 10px; 
    background: #000 url(back.svg) center center no-repeat; 
    background-size: 20px 20px; 
    box-shadow: 0 15px 35px #3232321a, 0 5px 15px #0000001a; 
    transition: transform 0.3s; 
}

.prev{
    background-image: url(back.svg) ; 
} 

.next{
    background-image: url(next.svg); 
}

.prev:hover, .next:hover {
    transform: scale(1.2); 
    background-color: #19b4fa;
    border: 1px solid #19b4fa;
}


@media screen and (max-width: 1024px) {
    .slider{
        margin-left: 10%; 
        
    }
}


@media screen and (max-width: 600px) {
    .slider{
        margin-left: 10%; 
    }
}

@media screen and (max-width: 480px) {
    .slider{
        margin-left: 20%; 
    }
}


